body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #000000;
  color: #333;
  line-height: 1.6;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  body {
      font-size: 16px;
  }
  .container {
      padding: 0 10px;
  }
}

@media screen and (max-width: 480px) {
  body {
      font-size: 14px;
  }
  .container {
      padding: 0 5px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 15px 0;
  padding: 0;
  color: #444;
}

p {
  margin: 0 0 15px 0;
}

a {
  color: #0099ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
