/* Estilos para el modal de carga de imagen */
.modal-overlay-cargar-imagen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content-cargar-imagen {
    background-color: #2b2626;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    width: 200px;
}

.modal-content-cargar-imagen p {
    margin-bottom: 20px;
    font-size: 18px;
    color: #ffffff;
}

.modal-content-cargar-imagen button {
    background-color: #28a745;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
}

.modal-content-cargar-imagen button:hover {
    background-color: #1f3624;
}

.modal-content-cargar-imagen .cancel-btn {
    background-color: #382123;
}

.modal-content-cargar-imagen .cancel-btn:hover {
    background-color: #c82333;
}

.hidden {
    display: none;
}
.confirmation-message {
  color: green;
  font-size: 16px;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
}

.modal-overlay-cargar-imagen .confirmation-message.hide {
  opacity: 0;
}
