/* Anuncio.css */
.anuncio-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7); /* Fondo oscuro con opacidad */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: fadeIn 0.5s ease-out;
    backdrop-filter: blur(10px);
  }
  
  .anuncio-content {
    background: #000000f5; /* Fondo blanco para el contenido del anuncio */
    color: rgb(255, 255, 254);
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    max-width: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    animation: slideIn 0.5s ease-out;
  }
  
  .anuncio-close-button {
    background: #721c24;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
  }
  
  .anuncio-close-button:hover {
    background: #5a1a1a;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(-20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  